import React, { Component } from "react";
import SearchBar from 'material-ui-search-bar';
import "./App.css";
import {Hero} from "react-landing-page";
import { Link } from 'react-router-dom';
import Button from "@material-ui/core/Button/Button";

class Search extends Component {
    constructor() {
        super();
        this.state = {
            val: "",
            test: "Initial"
        }
    }

    handleChange = (event) => {
        this.setState({
            val: event.target.value
        })
    }

    handleSearch = () => {
        this.props.history.push({
            pathname: "/searchres",
            search: this.state.val
        });
    }



    handleSubmit = () => {
            fetch("/api/lastname")
                .then((response) => response.json())
                .then((data) => this.setState({
                    test: data[0].lastName
                }))
    }


    render() {
        return (
            <Hero
                color="black"
                bg="white"
                backgroundImage="data:image/jpeg;base64,"
            >
                <div className="search-bar">
                    <h1 className={"header"}>Please Enter Employee Name</h1>
                    <SearchBar
                        value={this.state.val}
                        onChange={(value) => {this.setState({val: value})}}
                        onRequestSearch={() => this.handleSearch()}
                        style={{margin: '0 auto', maxWidth: 800}}/>
                </div>
                <Button component={Link} to={{
                    pathname: "/view",
                    state: {
                    route: "lastname"
                }}} >Click me</Button>
                <div>{this.state.test}</div>
            </Hero>
        )
    }
};

export default Search;
